import {
  FlatList,
  Image,
  ImageBackground,
  Linking,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import TaskComponent from './component/Task';
import {DUMMY_DATA, IMenuServices, IServices} from './helper.tsx';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import React, {useEffect, useState} from 'react';
import {useAppSelector} from '../../hooks/useRedux.ts';
import NavigationName from '../../constants/NavigationName.ts';
import {
  BgImageHomeHost,
  IcCeCa,
  IceAccount,
  IceConDrive,
  IceContract,
  IceFlow,
  IceForm,
  IceRequest,
  IceTask,
  IconAccount,
  IcWaveHand,
} from '../../assets/images/index.ts';
import TextCM from '../components/Text/index.tsx';
import Color from '../../constants/Color.ts';
import {handleUrl} from './handleDeepLink.tsx';
import {useAccountInfoServices} from '../../hooks/apiHooks/useAccountInfoServices.tsx';

const HomeScreenHostApp = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const {dataAccount} = useAppSelector(state => state.account);
  const [menuSwaps, setMenuSwaps] = useState<IMenuServices[]>([]);

  const AccountServices = useAccountInfoServices();

  useEffect(() => {
    const checkInitialURL = async () => {
      const url = await Linking.getInitialURL();
      if (url) {
        const dataFromUrl = handleUrl(url);
        if (dataFromUrl) {
          navigation.navigate(NavigationName.ERequest, dataFromUrl);
        }
      }
    };
    checkInitialURL();

    const linkingSubscription = Linking.addEventListener(
      'url',
      async ({url}) => {
        if (url) {
          const dataFromUrl = handleUrl(url);
          if (dataFromUrl) {
            navigation.navigate(NavigationName.ERequest, dataFromUrl);
          }
        }
      },
    );
    return () => linkingSubscription.remove();
  }, []);

  // useEffect(() => {
  //   getMenuView();
  // }, []);

  const getMenuView = async () => {
    const dataResp = (await AccountServices.getMenuView()) as IMenuServices[];
    if (dataResp) {
      const filterData = dataResp.map(menu => {
        return {
          ...menu,
          servicesList: menu?.servicesList?.map(service =>
            checkpathIcon(service),
          ),
        };
      });
      setMenuSwaps(filterData);
    }
  };

  const checkpathIcon = (service: IServices) => {
    switch (service.serviceName) {
      case 'switchApp.eContract.name':
        return {
          ...service,
          imgPath: <IceContract width={18} height={18} />,
          path: NavigationName.eContract,
        };
      case 'switchApp.eRequest.name':
        return {
          ...service,
          imgPath: <IceRequest width={18} height={18} />,
          path: NavigationName.ERequest,
        };
      case 'switchApp.eTask.name':
        return {
          ...service,
          imgPath: <IceTask width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eForm.name':
        return {
          ...service,
          imgPath: <IceForm width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eConDrive.name':
        return {
          ...service,
          imgPath: <IceConDrive width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eAccount.name':
        return {
          ...service,
          imgPath: <IceAccount width={18} height={18} />,
          path: null,
        };
      case 'switchApp.ceca.name':
        return {
          ...service,
          imgPath: <IcCeCa width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eFlow.name':
        return {
          ...service,
          imgPath: <IceFlow width={18} height={18} />,
          path: null,
        };
      default:
        return {
          ...service,
          imgPath: <IconAccount width={18} height={18} />,
          path: null,
        };
    }
  };

  const handleGoToAccountSetting = () => {
    navigation.navigate(NavigationName.AccountSetting);
  };

  return (
    <ImageBackground
      style={{
        minHeight: 247,
        backgroundColor: Color.White,
      }}
      resizeMode="cover"
      source={BgImageHomeHost}>
      <SafeAreaView style={styles.ctnHeader}>
        <TouchableOpacity
          onPress={handleGoToAccountSetting}
          style={[styles.ctnLeft]}>
          <View style={styles.ctnImage}>
            <Image
              width={40}
              height={40}
              source={{
                uri:
                  dataAccount.imageUrl ||
                  'https://lh3.googleusercontent.com/-cw77lUnOvmI/AAAAAAAAAAI/AAAAAAAAAAA/WMNck32dKbc/s181-c/104220521160525129167.jpg',
              }}
            />
          </View>
          <View style={{rowGap: 8}}>
            <TextCM style={[styles.text, {fontSize: 16}]}>
              {t('HO.home.hello')}
              <IcWaveHand width={20} height={20} />
            </TextCM>
            <TextCM
              numberOfLines={1}
              style={[styles.text, {fontSize: 12, width: 150}]}>
              {'' + dataAccount?.firstName + ' ' + dataAccount?.lastName}
            </TextCM>
          </View>
        </TouchableOpacity>
        <View style={{marginRight: 20}}>
          {/* <TouchableOpacity
              style={{
                position: 'relative',
              }}>
              <IcBell width={(22)} height={(22)} />
              <View style={styles.noti}>
                <Text style={styles.textNoti}>99+</Text>
              </View>
            </TouchableOpacity> */}
        </View>
      </SafeAreaView>
      <View style={styles.ctnHome}>
        {/* <View style={styles.ctnTextSearch}>
            <TextFieldCM
              label="Nhập để tìm kiếm"
              inputContainerStyle={{backgroundColor: Color.White}}
              viewIconLeft={<IcSearch />}
            />
          </View> */}
        {DUMMY_DATA.length > 0 && (
          <FlatList
            data={DUMMY_DATA}
            showsVerticalScrollIndicator={false}
            renderItem={item => (
              <View key={item.item.id}>
                <TaskComponent data={item.item} />
              </View>
            )}
          />
        )}
      </View>
    </ImageBackground>
  );
};

export default HomeScreenHostApp;
